.game-container {
  /* Some variables for color */
  --top-left-color: rgb(245, 63, 63);
  --top-left-shadow-color: rgb(255, 115, 115);
  
  --top-right-color: rgb(255, 255, 64);
  --top-right-shadow-color: rgb(255, 255, 98);
  
  --bottom-left-color: rgb(71, 240, 71);
  --bottom-left-shadow-color: rgb(71, 240, 71);
  
  --bottom-right-color: rgb(71, 218, 255);
  --bottom-right-shadow-color: rgb(122, 228, 255);

  --button-wrapper-bg-color: rgb(21, 92, 92);

  --center-menu-color: rgb(255, 255, 255);

  /* Styling for game-container */
  display: flex;
  justify-content: center;
  width: 100%;
  height: auto;
  max-width: 500px;
  padding: 20px;
}

.board-wrapper {
  background-color: var(--button-wrapper-bg-color);
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 250px;
  height: 250px;
  padding: 10px;
  border-radius: 50%;
  box-shadow: 3px 3px 10px 1px black;
}

.center-menu {
  width: 130px;
  height: 130px;
  background-color: var(--center-menu-color);
  box-shadow: 1px 1px 10px 1px black;
  position: absolute;
  top: 28%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  border-radius: 50%;
}

.player-button {
  width: 110px;
  height: 110px;
  box-shadow: inset 1px 1px 5px 1px black;
  transition: 0.2s;
}
.top-left {
  background-color: var(--top-left-color);
  border-radius: 100% 0% 0% 0%;
  margin-right: 2px;
  margin-bottom: 2px;
}
.top-right {
  background-color: var(--top-right-color);
  border-radius: 0% 100% 0% 0%;
  margin-left: 2px;
  margin-bottom: 2px;
}
.bottom-left {
  background-color: var(--bottom-left-color);
  border-radius: 0% 0% 0% 100%;
  margin-right: 2px;
  margin-top: 2px;
}
.bottom-right {
  background-color: var(--bottom-right-color);
  border-radius: 0% 0% 100% 0%;
  margin-left: 2px;
  margin-top: 2px;
}

.player-button:hover {
  cursor: pointer;
}
.top-left-active {
  background-color: var(--top-left-shadow-color);
  border: 0px;
  box-shadow: inset 1px 1px 5px 1px black, 0 0 5px var(--top-left-shadow-color), 0 0 20px var(--top-left-shadow-color), 0 0 40px var(--top-left-shadow-color);
}
.top-right-active {
  background-color: var(--top-right-shadow-color);
  border: 0px;
  box-shadow: inset 1px 1px 5px 1px black, 0 0 5px var(--top-right-shadow-color), 0 0 20px var(--top-right-shadow-color), 0 0 40px var(--top-right-shadow-color);
}
.bottom-left-active {
  background-color: var(--bottom-left-shadow-color);
  border: 0px;
  box-shadow: inset 1px 1px 5px 1px black, 0 0 5px var(--bottom-left-shadow-color), 0 0 20px var(--bottom-left-shadow-color), 0 0 40px var(--bottom-left-shadow-color);
}
.bottom-right-active {
  background-color: var(--bottom-right-shadow-color);
  border: 0px;
  box-shadow: inset 1px 1px 5px 1px black, 0 0 5px var(--bottom-right-shadow-color), 0 0 20px var(--bottom-right-shadow-color), 0 0 40px var(--bottom-right-shadow-color);
}

@media only screen and (min-width: 1000px) {
  .board-wrapper {
    width: 370px;
    height: 370px;
  }
  
  .player-button {
    width: 170px;
    height: 170px;
  }

  .center-menu {
    top: 33%;
  }
}